                                            <td v-else>
                                                <input type="text" name="txtcsUniqueName"
                                                    class="form-control txtcsUniqueName" v-model="item.UniqueName" />

                                            </td>
                                            <td class="text-center">

                                                <input v-if="item.IsEnableTerms == 1" type="checkbox" value="1"
                                                    disabled="disabled" checked="checked" class="assertermscondition" />

                                                <span v-else>{{ $t('NA') }}</span>

                                            </td>
                                            <td class="text-center">{{ item.AvailableItems }}</td>
                                            <td class="text-center">{{ item.HasUniqueNames == true ? 1 :
            item.PENDING_AVAILABLE_QUANTITY }}
                                            </td>

                                            <td class="text-center"
                                                v-if="item.HasUniqueNames == true || (item.HasUniqueNames == false && item.ChildUniqueId > 0)">


                                                <Field name="Requested Quantity" :rules="{ 'required': true }"
                                                    v-slot="{ errors }">

                                                    <input type="number" maxlength="4" min="1"
                                                        @input="checkQuantityGetRequisitionList(item, 'GetRequisitionList')"
                                                        v-model="item.RequestQuantity"
                                                        v-bind:class="{ 'txtitems form-control txtAssetQuentity': true, 'is-invalid': errors[0] || item.RequestQuantity == 0 ? true : false }" />
                                                </Field>

                                            </td>
                                            <td class="text-center" v-else>

                                                <input type="text" maxlength="4" data-numberonly="yes"
                                                    v-model="item.RequestQuantity"
                                                    v-bind:class="{ 'txtitems form-control': true, 'disabled': item.HasUniqueNames == true }" />


                                                <input type="hidden" name="assignedId" id="Assigned_hdn" value="" />


                                                <span></span>
                                            </td>
                                            <td class="text-center"
                                                v-if="item.ContainerList != null && item.ContainerList.Text == false">

                                                <select class="disabled form-control" readonly="readonly">
                                                    <option>{{ item.ContainerList.Text }}</option>
                                                </select>
                                            </td>
                                            <td class="text-center" v-else>
