                                                        class="fa fa-plus-circle text-info action-icon"></i></a>

                                                <a href="javascript:;" v-on:click="DeleteRecordFromPendingList(index)"
                                                    class="ancremoveData  recyclebin  enable" title="Remove"><i
                                                        class="fa fa-trash text-danger action-icon"></i></a>
                                            </td>
                                        </tr>


                                        <tr v-for="(child, index1) in item.childList"
                                            class="parentmain insertedrow bg-gold showassetcataloguegroup"
                                            :class="child.clsColor">
                                            <td>
                                                <span class="togglearrow arrowwhiteexpand_roles showcatalog"></span>

                                            </td>
                                            <td class="text-center">{{ child.AssetType }}</td>
                                            <td class="text-center">{{ child.ItemCode }}</td>
                                            <td class="text-center item-name">{{ child.CatalogName }}</td>
                                            <td class="text-center">{{ child.LocationName }}</td>
                                            <td class="text-center" v-if="child.ChildUniqueId <= 0">

                                                <div class="d-block py-2 text-center">OR</div>
                                                <div class="clearfix"></div>
                                                <input type="text"
                                                    class="form-control txtBarcodesearch col-10 text-center float-left"
                                                    v-on:click="SetUniqueNameData(child)" v-model="child.searchBarcode"
                                                    placeholder="Enter Barcode" />
                                                <a href="javascript:;" class="float-left green ancsearchbrcode m-2 ">
                                                    <i
                                                        class="searchicon enable fa fa-search action-icon text-success"></i>
                                                </a>

                                            </td>
                                            <td v-else>
                                                <input type="text" name="txtcsUniqueName"
                                                    class="form-control txtcsUniqueName" v-model="child.UniqueName" />

                                            </td>
                                            <td class="text-center">

                                                <input v-if="child.IsEnableTerms == 1" type="checkbox" value="1"
                                                    disabled="disabled" checked="checked" class="assertermscondition" />

                                                <span v-else>{{ $t('NA') }}</span>

                                            </td>
                                            <td class="text-center">{{ child.AvailableItems }}</td>
                                            <td class="text-center">{{ child.HasUniqueNames == true ? 1 :
            child.RequestQuantity
                                                }}</td>
                                            <td class="text-center"
                                                v-if="child.HasUniqueNames == true || (child.HasUniqueNames == false && child.ChildUniqueId > 0)">

                                                <input type="text" maxlength="4" data-numberonly="yes"
                                                    onkeyup="chkItems(this)" v-model="child.RequestQuantity"
                                                    class="txtitems form-control" />


                                                <input type="hidden" name="assignedId" id="Assigned_hdn" value="" />


                                                <span></span>

                                            </td>
                                            <td class="text-center" v-else>

                                                <input type="text" maxlength="4" data-numberonly="yes"
                                                    onkeyup="chkItems(this)" v-model="child.RequestQuantity"
                                                    v-bind:class="{ 'txtitems form-control': true, 'disabled': item.HasUniqueNames == true }" />


                                                <input type="hidden" name="assignedId" id="Assigned_hdn" value="" />


                                                <span></span>
                                            </td>
                                            <td class="text-center"
                                                v-if="child.ContainerList != null && child.ContainerList.Text == false">

                                                <select class="disabled form-control" readonly="readonly">
                                                    <option>{{ child.ContainerList.Text }}</option>
                                                </select>
                                            </td>
                                            <td class="text-center" v-else>
                                                <span>NA</span>
                                            </td>
                                            <td class="text-center">

                                                <a v-if="child.HasUniqueNames == false && child.ChildUniqueId <= 0"
                                                    href="javascript:;" class="ancaddmoredata  addchild enable"><i
                                                        class="fa fa-plus-circle text-info action-icon"></i></a>

                                                <a href="javascript:;" v-on:click="DeleteRecordFromPendingList(index1)"
                                                    class="ancremoveData  recyclebin  enable" title="Remove"><i
                                                        class="fa fa-trash text-danger action-icon"></i></a>
                                            </td>
